@media (min-width: 750px) {
	// .block-search {
	// 	max-width: 65%;
	// 	margin: 0 auto;
	// }
	.block-newsletter {
		input {
			margin: 0;
			@include margin-end(10px);
		}
		button {
			flex: 0 0 162px;
			max-width: 100%;
			height: 46px;
		}
	}
}
@media (min-width: 1300px) {
	.section-single-banner {
		.promo-block .block-inner {
			padding: 40px;
		}
	}
}